.a{
    height: 100vh;
    display: flex;
    align-items: center;
}

.a-left{
    flex:1 1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
}

.a-right{
    flex:1 1;
    padding-right: 1%;
}

.a-card.bg{
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: #555;
}

.a-card{
    width: 60%;
    height: 70vh;
    border-width: 30px;
    border-color: forestgreen;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
}

.a-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.a-title{
    font-weight: 400;
}
.a-sub{
    margin: 20px 0px;
}

.a-desc{
    font-weight: 300;
}

.a-cat{
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.a-cat-text{
    width: 75%;
}

.a-cat-title{
    font-weight: bold;
    color: #555;
}

.a-cat-img{
    width: 120px;
    height: 120px;
    border-radius: 20px;
    object-fit: cover;
}

@media screen and (max-width: 480px) {
    .a{
        flex-direction: column;
        text-align: center;
        margin-top: 50px;
    }

    .a-left{
        width: 100%;

    }

    .a-card{
        height: 30vh;
    }

    .a-card.bg, .a-cat{
        display: none;
    }

    .a-right{
        padding: 20px;
    }
}
.i{
    display: flex;
    height: 100vh;
}

.i-left{
    flex: 1 1;
    display: flex;
    justify-content: center;
}

.i-left-wrapper{
    margin-top: 5%;
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
}

.i-intro{
    font-size: 30px;
    font-weight: 300;
}

.i-name{
    font-size: 60px;
}

.i-title{
    height: 50px;
    overflow: hidden;
}

.i-desc{
    width: 100%;
}

.i-title-wrapper{
    height: 100%;
    -webkit-animation: move 10s ease-in-out infinite alternate;
            animation: move 10s ease-in-out infinite alternate;
}

@-webkit-keyframes move {
    25%{
        -webkit-transform: translateY(-50px);
                transform: translateY(-50px);
    }
    50%{
        -webkit-transform: translateY(-100px);
                transform: translateY(-100px);
    }
    75%{
        -webkit-transform: translateY(-150px);
                transform: translateY(-150px);
    }
    100%{
        -webkit-transform: translateY(-200px);
                transform: translateY(-200px);
    }
}

@keyframes move {
    25%{
        -webkit-transform: translateY(-50px);
                transform: translateY(-50px);
    }
    50%{
        -webkit-transform: translateY(-100px);
                transform: translateY(-100px);
    }
    75%{
        -webkit-transform: translateY(-150px);
                transform: translateY(-150px);
    }
    100%{
        -webkit-transform: translateY(-200px);
                transform: translateY(-200px);
    }
}

.i-title-item{
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #228b22;
    display: flex;
    align-items: center;
}

.i-right{
    flex: 1 1;
    display:flex;
    flex-direction: column;
    justify-content:flex-end;
    align-items: center;
    padding: 50px;
}

.i-card{
    overflow: hidden;
    border-style: double;
    border-color: forestgreen;
    border-width: 10px;
    border-radius: 30px;
}

.i-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.i-buttons{
    width: 70%;
    height: 15%;
    display: flex;
    justify-content: space-between;
    padding-bottom: 15%;
}

.al{
    display:flex;
    object-fit: contain;
}

.ag{
    display:flex;
    object-fit: contain;
}

.i-linkedin{
    display: block;
    width: 100%;
    height: 100%;
}

.i-github{
    display: block;
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 480px) {
    .i{
        flex-direction: column;
    }

    .i-left-wrapper{
        display: flex;
        padding: 10px;
        align-items: center;
        justify-content: center;
        text-align: center;
        height: 100%;
    }

    .i-desc{
        display: none;
    }
}
.pl{
    padding: 50px 70px;
    display: flex;
    flex-direction:column;
    align-items: center;
    text-align: center;
}

.pl-texts{
    width: 65%;
}

.pl-title{
    font-size: 50px;
    font-weight: 600;
}

.pl-desc{
    padding-bottom: 2%;
}

.pl-list{
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
}

@media screen and (max-width: 480px) {
    .pl{
        padding: 10px;
    }

    .pl-texts{
        width: 100%;
    }
}
.p-parent{
    height: 80vh;
    width: 100%;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.p-box{
    width: 35%;
    height: 60%;
    border: 2px solid rgb(238, 231, 231);
    border-radius: 10px 10px 0px 0px;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.p-browser{
    height: 20px;
    background-color: rgb(238, 231, 231);
    align-items: center;
    display: flex;
}

.p-circle{
    width: 6px;
    height: 6px;
    background-color: white;
    border-radius: 60%;
    margin: 3px;
}

.p-img{
    width: 100%;
    height: 100%; 
    object-fit: cover;  
}

.p-text{
   width: 35%;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
}

.p-title{

    width: 100%;
    font-weight: bold;
    margin: 5%;
}

.p-desc{

}

@media screen and (max-width: 480px) {
    .p-parent{
        width: 100%;
        justify-content: center;
        align-items: center;
    }
    .p-desc{
        font-size: small;
    }
    .p-box{
        width: 100%;
    }
    .p-text{
        width: 100%;
    }
}

.c{
    height: 100vh;
    position: relative;
}

.c-bg{
    width: 20px;
    height: 100%;
    background-color: forestgreen;
    position: absolute;
}

.c-wrapper{
    padding: 50px;
    display: flex;
}

.c-left{
    flex: 1 1;
}

.c-title{
    font-weight: bolder;
    font-size: 60px;
    margin: 10px 0px;
    width: 80%;
}

.c-info-items{
    display: flex;
    align-items: center;
    margin: 35px 0px;
    width: 70%;
}



.c-right{
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.c-desc{
    font-weight: 200;
}

form{
    margin-top: 20px;
}

input{
    width: 50%;
    height: 50px;
    border: none;
    border-bottom: 1px solid black;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}

textarea{
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}

button{
    border: 0;
    padding: 15px;
    background-color: forestgreen;
    color: white;
    font-weight: 500;
    cursor: pointer;
    transition: all .3s;
}

button:hover {
    box-shadow: 0 10px 10px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
}

button:active {
    -webkit-transform: scale(0.97);
            transform: scale(0.97);
    background-color: rgb(26, 104, 26);
}

@media screen and (max-width: 480px) {
    .c-wrapper{
        flex-direction: column;
        padding: 0px, 50px;
    }

    .c-title{
        font-size: 30px;
    }
    .c-bg{
        height: 110%;
    }

    .c-info-items{
        margin: 20px, 0px;
        width: 100%;
    }

    .c-desc{
        display: none;
    }

    form{
        margin-top: 0px;
        display: flex;
        flex-wrap: wrap;    
    }

    input{
        width: 35%;
        height: 40px;
        margin: 10px;
        margin-left: 0px;
    }

    button{
        margin-top: 10px;
    }
}


